<template>
    <g @mousedown="mousedown">
        <circle r="20" :fill="fill" stroke="black" stroke-width="2" :cx="vertex.x" :cy="vertex.y"></circle>
        <text font-size="14" text-anchor="middle" dominant-baseline="central" :x="vertex.x" :y="vertex.y">{{ this.vertex.name }}</text>
    </g>
</template>

<script>
export default {
    name: "Vertex",
    props: ["vertex", "fill"],
    methods: {
        mousedown: function() {
            this.$emit("selected", this.vertex);
        }
    }
};
</script>

<style scoped>
g {
    cursor: pointer;
    user-select: none;
}
</style>