<template>
    <component :is="routedComponent"></component>
</template>

<script>
import Graph from "./components/Graph.vue";
import DirectedGraph from "./directed_graph/DirectedGraph";

const routes = {
    "/": Graph,
    "/test": DirectedGraph
};

export default {
    data() {
        return { current: window.location.pathname };
    },
    computed: {
        routedComponent() {
            return routes[this.current];
        }
    }
};
</script>
