import { vertex, edge } from "../graph/Graph"

class Tool {
    constructor(context) {
        let { vertices, edges, graph, graphics } = { ...context };
        this.vertices = vertices;
        this.edges = edges;
        this.graph = graph;
        this.graphics = graphics;
        this.current_object = null;
        this.enabled = true;
    }

    name() { return "nameless"; }

    generate_graphics() {
        return null;
    }

    update_graphics(e) {
        if (!this.graphics.data) {
            this.graphics.data = this.generate_graphics();
        }
        var rect = e.currentTarget.getBoundingClientRect();
        this.graphics.x = e.clientX - rect.left - 25 / 2;
        this.graphics.y = e.clientY - rect.top - 25 / 2;
    }

    start() { }

    handle(e, o) {
        this.update_graphics(e);
        this.previous_object = this.current_object;
        this.current_object = o;
        if (this.object_filter(o)) {
            this.on_hit();
        } else {
            this.on_miss();
        }
    }

    on_hit() { }

    on_miss() { }

    apply() { }

    end() {
        this.graphics.data = null;
    }

    object_filter(obj) { return obj != null; }

    multistep() {
        return true;
    }
}

class AddVertexTool extends Tool {
    constructor(context) {
        super(context);
    }

    name() { return "Add Vertex"; }

    apply() {
        let chr = this.graphics.code;
        this.vertices.push(this.graph.add_vertex(chr, 30, 30));
        let code = chr.charCodeAt(0) + 1;
        this.graphics.code = String.fromCharCode(code);
        console.log("added vertex");
    }

    multistep() {
        return false;
    }
}

class RemoveVertexTool extends Tool {
    name() { return "Remover"; }

    generate_graphics() {
        return "<path d='M 0 0 L 25 25' stroke='red' stroke-width='2'/><path d='M 0 25 L 25 0' stroke='red' stroke-width='2'/>"
    }

    on_hit() {
        if (this.previous_object instanceof vertex) {
            this.previous_object.fill = "white";
        } else if (this.previous_object instanceof edge) {
            this.previous_object.fill = "black";
        }
        this.current_object.fill = "pink";
    }

    on_miss() {
        if (this.previous_object instanceof vertex) {
            this.previous_object.fill = "white";
        } else if (this.previous_object instanceof edge) {
            this.previous_object.fill = "black";
        }
    }

    apply() {
        if (this.current_object instanceof vertex) {
            for (let i = this.edges.length - 1; i >= 0; i--) {
                for (let e of this.current_object.edges.values()) {
                    if (e == this.edges[i]) {
                        this.graph.remove_edge(this.edges[i]);
                        this.edges.splice(i, 1);
                        break;
                    }
                }
            }
            for (let i = this.vertices.length - 1; i >= 0; i--) {
                if (this.vertices[i] == this.current_object) {
                    this.vertices.splice(i, 1);
                    break;
                }
            }
            this.graph.remove_vertex(this.current_object.name);
        } else if (this.current_object instanceof edge) {
            for (let i = this.edges.length - 1; i >= 0; i--) {
                if (this.current_object == this.edges[i]) {
                    this.graph.remove_edge(this.edges[i]);
                    this.edges.splice(i, 1);
                    break;
                }
            }
        }
        console.log("removed");
    }

    object_filter(obj) { return obj instanceof vertex || obj instanceof edge; }
}

class AddEdgeTool extends Tool {
    constructor(context) {
        super(context);
        this.selected_source = null;
    }

    name() { return "Add Edge"; }

    start() {
        this.graphics.x = 0;
        this.graphics.y = 0;
    }

    update_graphics(e) {
        if (!this.selected_source || this.selected_source == e.obj) {
            if (this.graphics.data) {
                this.graphics.data = "";
            }
            return;
        }
        let radius = 20;
        let v1x = this.selected_source.x;
        let v1y = this.selected_source.y;
        let v2x = 0;
        let v2y = 0;
        if (e.obj) {
            v2x = e.obj.x;
            v2y = e.obj.y;
        } else {
            var rect = e.currentTarget.getBoundingClientRect();
            v2x = e.clientX - rect.left;
            v2y = e.clientY - rect.top;
        }
        let degree = Math.atan2((v2y - v1y), (v2x - v1x));
        let cos_len = radius * Math.cos(degree);
        let sin_len = radius * Math.sin(degree);
        v1x = cos_len + v1x;
        v1y = sin_len + v1y;
        if (e.obj) {
            v2x -= cos_len;
            v2y -= sin_len;
        }

        if (isNaN(v1x) || isNaN(v1y) || isNaN(v2x) || isNaN(v2y)) return;

        this.graphics.data = `<path d='M ${v1x} ${v1y} L ${v2x} ${v2y}' stroke='black' stroke-width='3'/>`;
    }

    on_hit() {
        this.current_object.fill = "lightgreen";
    }

    on_miss() {
        if (this.previous_object instanceof vertex) {
            this.previous_object.fill = "white";
        }
    }

    apply() {
        if (this.selected_source) {
            if (this.current_object && this.selected_source != this.current_object && !this.graph.has_edge(this.selected_source, this.current_object)) {
                let edge = this.graph.add_edge(this.selected_source, this.current_object, 0);
                this.edges.push(edge);
                this.selected_source = null;
                console.log("added edge");
            }
        } else {
            if (this.current_object) {
                this.selected_source = this.current_object;
            }
        }
    }

    end() {
        super.end();
        this.selected_source = null;
    }

    object_filter(obj) { return obj instanceof vertex; }
}

/* ---------------------------------------------------------------- */
/*                             Dijkstra                             */
/* ---------------------------------------------------------------- */
import { Dijkstra } from "../graph/Dijkstra";

class DijkstraTool extends Tool {
    constructor(context) {
        super(context);
        this.dijk = new Dijkstra();
    }

    name() { return "Dijkstra Step"; }

    apply() {
        this.dijk.execute(this.graph);
        this.dijk.print_costs();
    }

    multistep() {
        return false;
    }
}

export { AddVertexTool, RemoveVertexTool, AddEdgeTool, DijkstraTool }