class Dijkstra {
    execute(graph, source) {
        if (!source || !graph) return;
        this.set_graph(graph, source);
        this.dijkstra();
    }

    set_graph(graph, source) {
        this.graph = graph;
        for (let vertex of graph.vertices.values()) {
            vertex.cost = Infinity;
            vertex.pred = null;
        }
        this.source = graph.get_vertex(source);
        this.source.cost = 0;
        this.iteration = 0;
        this.vQueue = Array.from(graph.vertices.values());
    }

    update_dist_pred(dists, preds) {
        let dist = [];
        let pred = [];
        for (let v of this.graph.get_sorted_vertices_names()) {
            let d = this.graph.get_vertex(v).cost;
            let p = this.graph.get_vertex(v).pred;
            if (p == null) {
                p = "-1";
            } else {
                p = p.name;
            }
            if (v == this.source.name) {
                d = "0";
                p = v;
            }
            if (d == Infinity) {
                d = "∞";
            }
            dist.push(String(d));
            pred.push(p);
        }
        dists.push(dist);
        preds.push(pred);
    }

    dijkstra() {
        if (this.source == null) return;
        this.cost_matrix = [];
        let title = ["Step", "N"];
        let vertices_name = this.graph.get_sorted_vertices_names();
        for (let i = vertices_name.length - 1; i >= 0; i--) {
            if (vertices_name[i] == this.source.name) {
                vertices_name.splice(i, 1);
            }
        }
        for (let name of vertices_name) {
            if (name != this.source.name) {
                title.push(`D(${name}), p(${name})`);
            }
        }
        this.cost_matrix.push(title);
        let current_nodes = [];
        let shortest_nodes = [];
        this.dists = [];
        this.preds = [];
        this.update_dist_pred(this.dists, this.preds);
        this.comments = ["Initialization"];
        let iter = 1;
        while (this.vQueue.length > 0) {
            let iteration_cost = [this.iteration];
            let min = this.get_min(this.vQueue);
            current_nodes.push(min.name);
            this.comments.push(`Iteration ${iter}: Min Vertex (${min.name}) with distance ${min.cost}`)
            iter++;
            for (let edge of min.edges.values()) {
                let to = (edge.v1 == min) ? edge.v2 : edge.v1;
                let new_cost = parseInt(edge.weight) + parseInt(min.cost);
                if (to.cost > new_cost) {
                    to.cost = new_cost;
                    to.pred = min;
                }
            }
            shortest_nodes.push(min.name);
            iteration_cost.push(current_nodes.join(""));
            for (let name of vertices_name) {
                if (shortest_nodes.includes(name)) {
                    iteration_cost.push(" ");
                } else {
                    let vertex = this.graph.get_vertex(name);
                    let cost = vertex.cost;
                    if (cost == Infinity) {
                        cost = "∞";
                        iteration_cost.push(`${cost}`);
                    } else {
                        iteration_cost.push(`${cost},${vertex.pred.name}`);
                    }
                }
            }
            this.vQueue.splice(this.vQueue.indexOf(min), 1);
            this.iteration++;
            this.cost_matrix.push(iteration_cost);
            this.update_dist_pred(this.dists, this.preds);
        }
    }

    get_min(verts) {
        if (!verts || verts.length == 0) return null;
        let min = verts[0];
        for (let v of verts) {
            if (v.cost < min.cost) min = v;
        }
        return min;
    }

    print_costs() {
        // console.log(this.cost_matrix);
        // console.log("--");
    }
}

export { Dijkstra }