<template>
    <button @click="clicked" :disabled="!enabled">{{ name }}</button>
</template>

<script>
export default {
    props: ["create", "obj"],
    data: function() {
        return {
            tool: null
        };
    },
    computed: {
        enabled: function() {
            if (this.tool) {
                return this.tool.enabled;
            }
            return true;
        },
        name: function() {
            if (this.tool) {
                return this.tool.name();
            }
            return "";
        }
    },
    mounted() {
        this.tool = new this.create(this.obj);
    },
    methods: {
        clicked: function() {
            this.$emit("activate_tool", this.tool);
        }
    }
};
</script>