class Vertex {
    constructor(name, x, y) {
        this.name = name;
        this.x = x;
        this.y = y;
        this.fill = "white";
        this.edges = new Map();
        this.cost = 0;
        this.pred = null;
    }
}

class Edge {
    constructor(v1, v2, weight) {
        this.v1 = v1;
        this.v2 = v2;
        this.fill = "black";
        this.weight = weight;
    }

    get_target(v) {
        if (v == this.v1) return this.v2;
        return this.v1;
    }
}

class Graph {
    constructor() {
        this.vertices = new Map();
    }

    get_sorted_vertices_names() {
        let vertices = Array.from(this.vertices.keys());
        vertices.sort();
        return vertices;
    }

    get_vertex(name) {
        return this.vertices.get(name);
    }

    has_vertex(name) {
        return this.vertices.has(name);
    }

    add_vertex(name, x = 0, y = 0) {
        if (this.has_vertex(name)) return null;
        let new_vertex = new Vertex(name, x, y);
        this.vertices.set(name, new_vertex);
        return new_vertex
    }

    remove_vertex(name) {
        let vertex = this.get_vertex(name);
        vertex.edges.forEach((edge, key) => {
            let to_vertex = (edge.v1 == vertex) ? edge.v2 : edge.v1;
            to_vertex.edges.delete(name);
            vertex.edges.delete(key);
        });
        this.vertices.delete(name);
    }

    set_vertex_name(v, new_name) {
        if (!new_name || this.has_vertex(new_name) || new_name.length == 0 || new_name == " ") return;
        this.vertices.set(new_name, v);
        this.vertices.delete(v.name);
        v.name = new_name;
    }

    has_edge(v1, v2) {
        return v1.edges.get(v2.name) || v2.edges.get(v1.name);
    }

    add_edge(v1, v2, weight = 0) {
        let new_edge = new Edge(v1, v2, weight);
        v1.edges.set(v2.name, new_edge);
        v2.edges.set(v1.name, new_edge);
        return new_edge;
    }

    remove_edge(edge) {
        edge.v1.edges.delete(edge.v2.name);
        edge.v2.edges.delete(edge.v1.name);
    }

    set_edge_weight(edge, weight) {
        if (weight && !isNaN(parseInt(weight))) {
            edge.weight = weight;
        }
    }

    print() {
        let str = "";
        for (let v of this.vertices.keys()) {
            str += v;
        }
        return str;
    }
}

export { Graph as graph, Vertex as vertex, Edge as edge }